<!-- eslint-disable -->
<template>
  <el-dialog
    title=""
    v-model="dialogVisible"
    width="600px"
    :close-on-click-modal="false"
    @close="handleClose"
  >
    <div class="upload-container">
      <!--  -->
      <div class="form-section">
        <div class="form-label required"></div>
        <div class="form-content">
          <el-radio-group v-model="form.fileType">
            <el-radio label="record"></el-radio>
            <el-radio label="abnormal"></el-radio>
          </el-radio-group>
        </div>
      </div>

      <!--  -->
      <div class="form-section">
        <div class="form-label"></div>
        <div class="form-content">
          <el-input
            v-model="form.remark"
            type="textarea"
            :rows="3"
            placeholder=""
          ></el-input>
        </div>
      </div>

      <!--  -->
      <div class="form-section">
        <div class="form-label required"></div>
        <div class="form-content">
          <el-upload
            ref="uploadRef"
            class="upload-area"
            action="#"
            :auto-upload="false"
            :on-change="handleFileChange"
            :on-remove="handleFileRemove"
            :on-preview="handleFilePreview"
            :file-list="fileList"
            :limit="5"
            multiple
            drag
            accept=".pdf,.jpg,.jpeg,.png"
          >
            <el-icon class="el-icon-upload"><upload-filled /></el-icon>
            <div class="el-upload__text">
              <em></em>
            </div>
            <template #tip>
              <div class="el-upload__tip">
                 .pdf.jpg.jpeg.png  10MB
              </div>
            </template>
          </el-upload>
        </div>
      </div>

      <!--  -->
      <div class="form-section" v-if="previewVisible">
        <div class="form-label"></div>
        <div class="form-content">
          <div class="preview-container">
            <div class="preview-header">
              <div class="preview-title">{{ previewFile.name }}</div>
              <el-button type="text" @click="closePreview"></el-button>
            </div>
            <div class="preview-content">
              <img v-if="isImageFile(previewFile)" :src="previewUrl" class="preview-image" />
              <div v-else class="preview-placeholder">
                <document class="preview-icon" />
                <div>{{ previewFile.name }}</div>
                <el-button type="primary" size="small" @click="downloadFile"></el-button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <template #footer>
      <span class="dialog-footer">
        <el-button @click="handleClose"></el-button>
        <el-button type="primary" @click="handleSubmit" :disabled="!isValid" :loading="uploading">
          {{ uploading ? '...' : '' }}
        </el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script>
import { ref, computed, watch } from 'vue';
import { UploadFilled, Document } from '@element-plus/icons-vue';
import { ElMessage } from 'element-plus';

export default {
  name: 'TrainFileUploadDialog',
  components: {
    UploadFilled,
    Document
  },
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    trainId: {
      type: [String, Number],
      default: ''
    }
  },
  emits: ['update:visible', 'submit', 'cancel'],
  setup(props, { emit }) {
    const dialogVisible = computed({
      get: () => props.visible,
      set: (val) => emit('update:visible', val)
    });

    const uploadRef = ref(null);
    const uploading = ref(false);

    // 
    const form = ref({
      fileType: 'record', // 
      remark: '',
      files: []
    });

    // 
    const fileList = ref([]);

    // 
    const previewVisible = ref(false);
    const previewFile = ref({});
    const previewUrl = ref('');

    // 
    const isValid = computed(() => {
      return form.value.fileType && form.value.files.length > 0;
    });

    // 
    const handleFileChange = (file) => {
      // 
      const validTypes = ['application/pdf', 'image/jpeg', 'image/png', 'image/jpg'];
      const fileType = file.raw.type;
      
      if (!validTypes.includes(fileType)) {
        ElMessage.error('');
        return false;
      }

      // 10MB
      const maxSize = 10 * 1024 * 1024;
      if (file.raw.size > maxSize) {
        ElMessage.error(' 10MB');
        return false;
      }

      // 
      if (!form.value.files.includes(file.raw)) {
        form.value.files.push(file.raw);
      }
    };

    // 
    const handleFileRemove = (file) => {
      const index = form.value.files.findIndex(f => f === file.raw);
      if (index > -1) {
        form.value.files.splice(index, 1);
      }
    };

    // 
    const isImageFile = (file) => {
      if (!file || !file.name) return false;
      const extension = file.name.split('.').pop().toLowerCase();
      return ['jpg', 'jpeg', 'png'].includes(extension);
    };

    // 
    const handleFilePreview = (file) => {
      previewFile.value = file;
      if (isImageFile(file)) {
        // URL
        previewUrl.value = URL.createObjectURL(file.raw);
      } else {
        // 
        previewUrl.value = '';
      }
      previewVisible.value = true;
    };

    // 
    const closePreview = () => {
      previewVisible.value = false;
      if (previewUrl.value) {
        URL.revokeObjectURL(previewUrl.value);
        previewUrl.value = '';
      }
    };

    // 
    const downloadFile = () => {
      if (previewFile.value && previewFile.value.raw) {
        const url = URL.createObjectURL(previewFile.value.raw);
        const a = document.createElement('a');
        a.href = url;
        a.download = previewFile.value.name;
        document.body.appendChild(a);
        a.click();
        setTimeout(() => {
          document.body.removeChild(a);
          URL.revokeObjectURL(url);
        }, 0);
      }
    };

    // 
    const handleSubmit = async () => {
      if (!isValid.value) {
        ElMessage.warning('');
        return;
      }

      uploading.value = true;
      try {
        // 
        const formData = {
          trainId: props.trainId,
          fileTag: form.value.fileType,
          remark: form.value.remark,
          files: form.value.files
        };

        // 
        emit('submit', formData);
      } catch (error) {
        console.error(':', error);
        ElMessage.error('');
      } finally {
        uploading.value = false;
      }
    };

    // 
    const handleClose = () => {
      // URL
      if (previewUrl.value) {
        URL.revokeObjectURL(previewUrl.value);
      }
      
      // 
      form.value = {
        fileType: 'record',
        remark: '',
        files: []
      };
      fileList.value = [];
      previewVisible.value = false;
      
      // 
      emit('cancel');
      dialogVisible.value = false;
    };

    // visible
    watch(() => props.visible, (newVal) => {
      if (newVal) {
        // 
        form.value = {
          fileType: 'record',
          remark: '',
          files: []
        };
        fileList.value = [];
        previewVisible.value = false;
      }
    });

    return {
      dialogVisible,
      form,
      fileList,
      uploadRef,
      uploading,
      previewVisible,
      previewFile,
      previewUrl,
      isValid,
      handleFileChange,
      handleFileRemove,
      handleFilePreview,
      isImageFile,
      closePreview,
      downloadFile,
      handleSubmit,
      handleClose
    };
  }
};
</script>

<style scoped>
.upload-container {
  padding: 20px 0;
}

.form-section {
  display: flex;
  margin-bottom: 20px;
}

.form-label {
  width: 100px;
  text-align: right;
  padding-right: 12px;
  color: #606266;
  line-height: 32px;
}

.required::before {
  content: '*';
  color: #f56c6c;
  margin-right: 4px;
}

.form-content {
  flex: 1;
}

.upload-area {
  width: 100%;
}

.el-upload {
  width: 100%;
}

.el-upload-dragger {
  width: 100%;
}

.dialog-footer {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

.preview-container {
  border: 1px solid #e4e7ed;
  border-radius: 4px;
  padding: 10px;
  background-color: #f8f8f8;
}

.preview-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid #e4e7ed;
}

.preview-title {
  font-weight: bold;
  color: #303133;
}

.preview-content {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 200px;
  background-color: #fff;
  border-radius: 4px;
}

.preview-image {
  max-width: 100%;
  max-height: 300px;
  object-fit: contain;
}

.preview-placeholder {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  color: #909399;
}

.preview-icon {
  font-size: 48px;
}
</style> 